












































import { Vue, Component, Emit } from "vue-property-decorator";

@Component({
  name: 'Navigation',
})
export default class Navigation extends Vue {
    @Emit('new-game')
    newGame() {
        return true;
    }

    @Emit('show-help')
    showHelp() {
        return true;
    }

    @Emit('show-solution')
    showSolution() {
        return true;
    }
}
